<template>
    <div>
        <div>
            dev环境跨域 --需修改proxyTable配置即可
            proxyTable: {  <br>
                '/API_SOHO': { // 要代理的接口名<br>
                target: 'http://pv.sohu.com', // 要代理的接口地址<br>
                changeOrigin: true, // 允许跨域<br>
                pathRewrite: { '^/API_SOHO': '' } // 接口名重写 }<br>
            },
        </div>
        <div>
            prod环境跨域 --修改Nginx的配置文件 xxx.conf<br>
            location /api {  rewrite  ^.+api/?(.*)$ /$1 break; include  uwsgi_params; proxy_pass   https://www.xxxxx.cn:444; #此处修改为自己的请求地址}<br>
            ###两处api为本地开发时，在config/index.js中的proxyTable: {}配置的请求代理        ###根据具体情况修改
        </div>

    </div>
</template>

<script>
    export default {
        name: "VueCrossDomain"
    }
</script>

<style scoped>

</style>



